<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/leader" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/studentManagement"
                        tag="span">学生信息管理管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/teacherManagement"
                        tag="span">教师信息管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/courseManagement"
                        tag="span">课程信息管理</router-link></el-menu-item>
                        <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>

            </el-menu>
        </el-footer>

        <el-main>

            <el-form :model="inputData" :inline="true" label-width="50px" style="width: 500px;"
                class="demo-form-inline">
                <el-form-item prop="key">
                    <el-input v-model="inputData" placeholder="请输入关键字以搜索"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="likeGet">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="dialogFormVisible2 = true">添加</el-button>
                </el-form-item>
            </el-form>



            <el-table :data="teacherData" style="width: 100%">
                <el-table-column prop="teacherName" label="姓名" width="300">
                </el-table-column>
                <el-table-column prop="teacherSex" label="性别" width="300">
                </el-table-column>
                <el-table-column prop="teacherNum" label="教师编号" width="300">
                </el-table-column>
                <el-table-column label="操作" width="300">
                    <template slot-scope="scope">
                        <el-button type="text" @click="dialogFormVisible = true">修改</el-button>
                        <el-dialog title="修改" :visible.sync="dialogFormVisible">
                            <el-form :model="form">
                                <el-form-item label="教师编号" :label-width="formLabelWidth">
                                    <el-input v-model="form.teacherNum" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="教师姓名" :label-width="formLabelWidth">
                                    <el-input v-model="form.teacherName" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="教师性别" :label-width="formLabelWidth">
                                    <el-input v-model="form.teacherSex" autocomplete="off"></el-input>
                                </el-form-item>
                               
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="reset">取 消</el-button>
                                <el-button type="primary" @click="formUpdate(scope.row.teacherNum)">确 定</el-button>
                            </div>
                        </el-dialog>
                        <el-button @click="deleteById(scope.row.teacherNum)" type="text" size="medium">删除</el-button>
                    </template>
                </el-table-column>


            </el-table>
            <!-- 对话框，移出 el-form-item -->
            <el-dialog title="添加" :visible.sync="dialogFormVisible2">
                <el-form :model="form2">
                    <el-form-item label="教师姓名" :label-width="formLabelWidth">
                        <el-input v-model="form2.teacherName" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" :label-width="formLabelWidth">
                        <el-input v-model="form2.teacherSex" autocomplete="off"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="教师编号" :label-width="formLabelWidth">
                        <el-input v-model="form2.teacherNum" autocomplete="off"></el-input>
                    </el-form-item> -->
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="reset">取 消</el-button>
                    <el-button type="primary" @click="insertExam">确 定</el-button>
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            inputData: '',
            //绑定添加数据
            form2: {
                teacherName: '',
                teacherNum: '',
                teacherSex: '',
            },
            teacherData: [],
            //绑定修改数据
            form: {
                teacherName: '',
                teacherNum: '',
                teacherSex: '',
            },

            //试卷详情

            dialogFormVisible: false,
            dialogFormVisible2: false,
            dialogVisible: false,
            formLabelWidth: '120px'
        }
    },
    methods: {
        //查询全部
        getAll() {
            axios.get("/api/teacher").then((result) => {
                this. teacherData = result.data.data;
            });
        },
        //模糊查询
        likeGet() {
            axios.get(`/api/teacher/inputData/${this.inputData}`).then((result) => {
                this. teacherData = result.data.data;
            });

        },
        //修改表单
        formUpdate() {
            axios.put("/api/teacher", this.form).then((result) => {
                if (result.data.code == 1) {
                    this.dialogFormVisible = false;
                    this.open2();
                    this.getAll();
                    this.reset();
                }
                else {
                    this.open3();

                }
            });

        },
        //删除数据
        deleteById(id) {
            axios.delete(`/api/teacher/${id}`).then((result) => {
                if (result.data.code == 1) {
                    this.open2();
                    this.getAll();
                }
                else {
                    this.open3();
                }


            });

        },
        //添加试卷
        insertExam() {
            axios.post("/api/teacher", this.form2).then((result) => {
                if (result.data.code == 1) {
                    this.getAll();
                    this.open2();
                    this.reset();

                }
                else {
                    this.open3();
                }

            });

        },
        //重置
        reset() {
            this.form = '';
            this.dialogFormVisible = false
        },
        //成功提示
        open2() {
            this.$message({
                message: '恭喜你，这是一条成功消息',
                type: 'success'
            });
        },
        //错误提示
        open3() {
            this.$message({
                message: '警告哦，这是一条警告消息',
                type: 'warning'
            });
        },

    },
    mounted() {
        this.getAll();

    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh;
    /* 至少占据整个视口高度 */
    max-height: 1200px;
    /* 最大高度限制 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
}

.el-header,
.el-footer {
    flex: 0 0 auto;
    /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
}

.el-main {
    flex: 1 1 auto;
    /* 自动占据剩余空间 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
}
</style>
